import React, { Component } from 'react'
import {Button} from 'antd'
import BaseContent from '@/components/BaseContent'
import BaseTable from '@/components/BaseTable'

export default class QuestionsType extends Component {
  getColumns () {
    const columns = [{
      title: 'ID',
      dataIndex: 'contractNumber',
      key: 'contractNumber',
    }, {
      title: '',
      dataIndex: 'contractName',
      key: 'contractName',
    }, {
      title: '',
      dataIndex: 'contractName',
      key: 'contractName',
    }];
    return columns
  }

  render() {
    return (
      <div>
        <BaseContent>
          <header style={{overflow: 'hidden'}}>
            
          </header>
          <React.Fragment>
              <BaseTable
                  columns={this.getColumns()}
                  buttons={
                    [
                      <Button type="primary" icon="plus" size="large" style={{float: 'right'}}></Button>
                    ]
                  }
                  pagination={{
                      current: 0,
                      pageSize: 10,
                      total: 100
                  }}
                  wrapStyle={{
                      padding: 0,
                  }}
              />
          </React.Fragment>
        </BaseContent>
      </div>
    )
  }
}
